
.site-footer {
   
    padding-top: 3rem;   
    font-size: rem(12);
    text-align: center;
}

.qqservice {
    position: fixed;
    bottom: 1rem;
    right: 1rem;
    z-index: 2000;    
}

.totop {
    display: block;
    width: 45px;
    height: 45px; 
    line-height: 43px;
    border: 1px solid #000;   
    text-align: center;
    transition: all .2s;
    background-color: #fff;
    color: #000;
    @include respond-above(md) {
        width: 60px;
        height: 60px; 
        line-height: 56px;
        border: 2px solid #000;  
     }
    .iconfont{
        font-size: 22px;
        @include respond-above(md) {
            font-size: 28px;
         }
    }
   

    @include hover {
        border-color: $color-primary;  
        color:  $color-primary;  
    }

    @include respond-above(sm) {
        bottom: 2rem;
        right: 2rem;
    }
}
